@import url(https://fonts.googleapis.com/css2?family=Comfortaa&family=Josefin+Sans&family=Zilla+Slab&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

header {
  background: #B78D7B;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

header .container {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

header .left > *,
header .right > * {
  margin: 0 15px;
}

header .left {
  display: flex;
}

header .left .logo svg {
  height: 40px;
  width: 40px;
  fill: #fff;
  vertical-align: middle;
}

header .title {
  height: -webkit-max-content;
  height: max-content;
}

header .title h1 {
  font-size: 32px;
  text-transform: uppercase;
  letter-spacing: 4px;
}

header .right {
  display: flex;
}

header .right a {
  display: inline-block;
  width: 40px;
  position: relative;
}

header .right a .counter {
  display: none;
}

header .fa {
  position: relative;
  color: #fff;
  font-size: 40px;
  font-weight: 100;
}

header .fa.addedItem + .counter {
  font-size: 13px;
  font-weight: 900;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 0;
  right: 0;
  height: 26px;
  width: 26px;
  border-radius: 50%;
  background: #ff8b01;
  -webkit-transform: translate(50%, -20%);
          transform: translate(50%, -20%);
}

header #burger {
  display: inline-block;
  position: relative;
  width: 40px;
  height: 40px;
  cursor: pointer;
  z-index: 1;
}

header #burger:hover span, header #burger:hover:before, header #burger:hover:after {
  background: #000;
}

header #burger > span {
  background: #fff;
  position: absolute;
  width: 100%;
  height: 4px;
  border-radius: 2px;
  left: 0;
  top: 18px;
  transition: all 0.3s linear;
}

header #burger:before, header #burger:after {
  content: '';
  background: #fff;
  position: absolute;
  width: 100%;
  height: 4px;
  border-radius: 3px;
  transition: all 0.3s linear;
}

header #burger:before {
  top: 5px;
}

header #burger:after {
  bottom: 5px;
}

header #burger.active span {
  -webkit-transform: scale(0);
          transform: scale(0);
}

header #burger.active:before {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  top: 18px;
}

header #burger.active:after {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
  bottom: 18px;
}
